import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";
import i1 from "./assets/images/1.png";

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: #1E90FF;
  padding: 10px;
  font-weight: bold;
  color: #ffffff;
  width: 250px;
  cursor: pointer;
  box-shadow: 2px 8px 4px -2px rgba(30, 144, 255, 1);
  -webkit-box-shadow: 2px 3px 10px -2px rgba(30, 144, 255, 1);
  -moz-box-shadow: 2px 8px 4px -2px rgba(30, 144, 255, 1);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
  :hover {
    -webkit-box-shadow: 2px 3px 40px -2px rgba(30, 144, 255, 1);
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: column;
  }
`;

export const StyledImg = styled.img`
  width: 200px;
  height: 200px;
  @media (min-width: 767px) {
    width: 300px;
    height: 300px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [feedback, setFeedback] = useState("I can paint an unique 'Meta Lisa' for you...");
  const [claimingNft, setClaimingNft] = useState(false);

  const claimNFTs = (_amount) => {
    if (_amount <= 0) {
      return;
    }
    setFeedback("Getting my digital brushes ready...");
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(blockchain.account, _amount)
      .send({
        gasLimit: "285000",
        to: "0x865210fED5eE89A113D260a80D7fbF7B85255C11",
        from: blockchain.account,
        value: blockchain.web3.utils.toWei((10 * _amount).toString(), "ether"),
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("It seems you don't want me to paint anymore...");
        setClaimingNft(false);
      })
      .then((receipt) => {
        setFeedback(
          "Unique 'Meta Lisa' successfully painted. Moving to your collected items (or Hidden items) on opensea.io"
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen style={{ backgroundColor: "var(--black)" }}>
      <s.Container flex={1} ai={"left"} style={{ padding: 12 }}>
        <ResponsiveWrapper flex={1} style={{ padding: 12 }}>
          <s.Container flex={1} jc={"left"} ai={"left"}>
                <s.TextDescription style={{ textAlign: "left", fontSize: 12, color: "dodgerblue", fontWeight: "bold" }}>
                HALda Vinci Program Loaded.
                </s.TextDescription>
                <s.SpacerMedium />
            <s.TextTitle
              style={{ textAlign: "left", fontSize: 12, color: "white" }}
            >
              NFT Paintings: {data.totalSupply}/10000 
              <s.SpacerXSmall />
                <s.TextDescription style={{ textAlign: "left", fontSize: 12, color: "dark-grey" }}>
                  - Connect wallet for current numbers
                </s.TextDescription>
            </s.TextTitle>
          </s.Container>
          <s.Container
            flex={1}
            jc={"left"}
            ai={"left"}
            style={{ backgroundColor: "#000000", padding: 0 }}
          >
            {Number(data.totalSupply) == 10000 ? (
              <>
                <s.TextTitle style={{ textAlign: "left", fontSize: 12 }}>
                10000 unique 'Meta Lisa' painted successfully. Program shut down.
                </s.TextTitle>
                <s.SpacerSmall />
                <s.TextTitle style={{ textAlign: "left", fontSize: 12 }}>
                  You can find the collection on{" "}
                  <a
                    target={""}
                    href={"https://opensea.io/collection/metalisa"}
                  >
                    Opensea.io
                  </a>
                </s.TextTitle>
              </>
            ) : (
              <>
                <s.TextTitle style={{ textAlign: "left", fontSize: 12, fontWeight: "bold" }}>
                  {feedback}
                </s.TextTitle>
                <s.SpacerMedium />
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      CONNECT POLYGON WALLET
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription style={{ textAlign: "center", fontSize: 12 }}>
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <s.Container ai={"center"} jc={"center"} fd={"row"}>
                    <StyledButton
                      disabled={claimingNft ? 1 : 0}
                      onClick={(e) => {
                        e.preventDefault();
                        claimNFTs(1);
                        getData();
                      }}
                    >
                      {claimingNft ? "Painting..." : "Buy 1 'Meta Lisa' NFT Painting"}
                    </StyledButton>
                  </s.Container>
                )}
               </>
            )}
          </s.Container>
        </ResponsiveWrapper>
        </s.Container>
    </s.Screen>
  );
}

export default App;
